import {combineReducers } from 'redux';
import stateReducer from './stateReducer';
import userReducer from './userReducer';
import ministryReducer from './ministriesReducer';
import { LOAD, SAVE } from 'redux-storage';

export function HigherOrderReducer(reducers, initialState) {
    let higherState = {
        state: initialState
    };
    
    return function (state = {}, action) {
        switch (action.type){
            case LOAD:
                higherState.state = action.payload;
                return higherState.state;
            case SAVE:
                return reducers(state, action);
            
            default:
                return reducers(state, action);
        }
    };
}

export default combineReducers ({stateReducer, userReducer, ministryReducer});

