import React, { Component } from 'react';
import './About.scss';
import DisplayMsg from '../LandingPage/DisplaySlides';

export default class About extends Component {

    initAll = () =>{
        const menu = document.querySelector("#rolling-menu");
        const circle = document.querySelector("#rolling-menu .circle");
        const line = document.querySelector("#rolling-menu .line");

        circle.addEventListener("click", () => {
            menu.classList.toggle("active");
            if(line.classList.contains("initial") ) {
                line.classList.remove("initial");
            }
        })

    }
    
        
    render() {
       //this.initAll()

        return (
            <div className={'about'}>
               About Us
            </div>
        )
    }
}
