import axios from 'axios';

let axiosConfig = {
    withCredentials: true,
};

export const getUrl = () => {
    return  'https://localhost:44310';
};

export const getAxiosInstance = () => {
    var url = getUrl();
    var headers = { headerType: 'example header'}
    return axios.create({ baseURL: url , headers});
};

export const getUser = async (username, password) => {
    const data = {
        'Username': username,
        'Password': password
    };
    
    var resp = await getAxiosInstance().post(`${getUrl()}${'/api/Users'}`, data, axiosConfig);
    return  resp;
};

export const login = async (username,pwd) =>{
    try
    {
        await getUser(username, pwd);    
        return true;          
    }catch(err){
        return false;
    }
}

export const logout = async () =>{
    try
    { //await clearToken();    
        return true;          
    }catch(err){
        return false;
    }
}
