import * as actionTypes from '../actions/actionTypes';

export default (state = {
    isLoggedIn:false,
    Title:null,
    FirstName:null,
    LastName:null,
    Email:null,
    Role:null,
    CellNumber:null,
    Region:{
        Province:null,
        RegionName:null
    },
    Province:null,
    Town:null,
    Church:{
        Branch:null,
        Province:null
    },
    Surbub:null,
    Designation:null,
    Username:null,
    Password:null,
    Comments:null,
    Dissciples: []

}, action) => {
    switch(action.type){
        case actionTypes.GET_USER_SUCCESS:
            console.log('action', action);
            let newState = state;
            let updatedState = action.data;
            //newState.isLoggedIn = updatedState.isLoggedIn;
            newState.Title =  updatedState.Title;
            newState.FirstName = updatedState.FirstName;
            newState.LastName = updatedState.LastName; 
            newState.Email = updatedState.Email;
            newState.Role = updatedState.Role;
            newState.CellNumber = updatedState.CellNumber; 
            newState.Region = updatedState.Region;
            newState.Town = updatedState.Town;
            newState.Church = updatedState.Church;
            newState.Surbub = updatedState.Surbub;
            newState.Designation = updatedState.Designation; 
            newState.Username =  updatedState.Username;
            newState.Password = updatedState.Password;
            newState.Comments = updatedState.Comments;
            newState.Dissciples = updatedState.Dissciples;
            return {
               ...newState
            }
        // case actionTypes.POST_USER_SUCCESS:
        //     return executePostDataSuccess(state, action);
        // case actionTypes.PUT_USER_SUCCESS:
        //     return executePutDataSuccess(state, action);
        // case actionTypes.DELETE_USER_SUCCESS:
        //     return executeDeleteDataSuccess(state, action);
        default:
            console.log(' default action', action);
            return state;
    }
};