import { createStore, applyMiddleware, compose } from 'redux';
import thunk from 'redux-thunk';
import * as storage from 'redux-storage';
import { composeWithDevTools } from 'redux-devtools-extension';
import createEngine from 'redux-storage-engine-sessionstorage';
import mainReducer, { HigherOrderReducer } from '../reducers';

import apiRMiddleware  from './apiHelper';

export function getReduxStore() {
    const engine = createEngine('currentState');
    const storageMiddleware = storage.createMiddleware(engine);
    const reducer = HigherOrderReducer(mainReducer);
    const enhancers = compose(
        applyMiddleware(thunk,apiRMiddleware,storageMiddleware)
    );

    var cachedStore = typeof window !== 'undefined'
        ? !!sessionStorage.getItem('currentState')
        : false;

    const composeEnhancers = composeWithDevTools({
        // Specify name here, actionsBlacklist, actionsCreators and other options if needed
        actionsBlacklist: [
            'REDUX_STORAGE_LOAD',
            'REDUX_STORAGE_SAVE'
        ]
    });
    let preLoadedState = {};
    
    if(cachedStore){
        preLoadedState = JSON.parse(sessionStorage.getItem('currentState'));
    }    

    return createStore(reducer, preLoadedState, composeEnhancers(enhancers));
}
