import React, { Component } from 'react'
import './Sermons.scss';
import {Player} from './audioPlayer/Player';
import VideoComponent from './video/Video';

import audioIcon from '../../images/audio.png';
import movieIcon from '../../images/movie.png';

class Sermons extends Component {
  constructor(props){
    super(props);

    this.state = ({
      isAudioPodcast: true
    })
}

togglePodcast(type){
  this.setState({
      isAudioPodcast : type
  })
}

  doTheHonors = () =>{
    // Get all the tabs
    const tabs = document.querySelectorAll('.tab');

    tabs.forEach(clickedTab => {
        // Add onClick event listener on each tab
        clickedTab.addEventListener('click', () => {
            // Remove the active class from all the tabs (this acts as a "hard" reset)
            tabs.forEach(tab => {
                tab.classList.remove('active');
            });

            // Add the active class on the clicked tab
            clickedTab.classList.add('active');
            const clickedTabBGColor = getComputedStyle(clickedTab).getPropertyValue('color');
            document.body.style.background = clickedTabBGColor;
        });
    });
  }

  render() {
    
    let {isAudioPodcast} = this.state;
    let nav_name1="Audio", nav_name2="Video"; 
    return (
      <div className={'sermon'}>
        {isAudioPodcast && <Player/>}
        {!isAudioPodcast &&  <VideoComponent/>}
        <div className={'bottom-nav'}>
            <div className="tab-nav-container">
                <div className="tab active purple" onClick={()=>{this.doTheHonors();this.togglePodcast(true) }}>
                    <i class="fas fa-home"><img class="sticky-search" src={audioIcon} /></i>
                    <p>{nav_name1}</p>
                </div>
                <div className="tab pink"  onClick={()=>{this.doTheHonors();this.togglePodcast(false) }}>
                    <i class="far fa-heart"><img class="sticky-filters" src={movieIcon} /></i>
                    <p>{nav_name2}</p>
                </div>
            </div>
      </div>
 
      </div>
    )
  }
}


export default Sermons;
