import React, { Component } from 'react';
import './Registration.scss'

export default class Registration extends Component {
    constructor(props){
        super(props);   

        this.state = {
            amount: 580,
            firstName:null,
            email:null,
            event_name: 'Event',
            province:null,
            phone:null
        }
        
    }

    addTotal = () => {
        var sel = document.getElementById('item_name');
   
        var opt = 0;    
        for ( var i = 0, len = sel.options.length; i < len; i++ ) {
            opt = sel.options[i];
            if ( opt.selected === true ) {
                break;
            }
        }

        var dash = (opt.value).indexOf('-');
        var price = (opt.value).substring(0,dash);
        var event = (opt.value).substring(dash+1);
        this.setState({amount:price,
            event_name:event});
    }

    setProvince = () => {
        var sel = document.getElementById('province');
   
        var opt = 0;
        for ( var i = 0, len = sel.options.length; i < len; i++ ) {
            opt = sel.options[i];
            if ( opt.selected === true ) {
                break;
            }
        }
        this.setState({province:opt.value});
    }

    addName = () => {
        var name = document.getElementById('name_first').value;
        this.setState({firstName:name});
    }

    addPhone = () => {
        var param = document.getElementById('phone').value;
        this.setState({phone:param});
    }

    addEmail = () => {
        var param = document.getElementById('email_address').value;
        this.setState({email:param});
    }

    changeCustomAmount = () => {
        var param = document.getElementById('amount').value;
        this.setState({amount:param});
    }
    
    render() {

       let {amount=0, firstName, email,event_name,province,phone} = this.state;
        function gotoPayfast( aform_reference ) {
            var aform = aform_reference;
            aform['amount'].value = Math.round( aform['amount'].value*Math.pow( 10,2 ) )/Math.pow( 10,2 );
            aform['custom_amount'].value = aform['custom_amount'].value.replace( /^\s+|\s+$/g,"" );
            if( !aform['custom_amount'].value || 0 === aform['custom_amount'].value.length || /^\s*$/.test( aform['custom_amount'].value ) ) {
                alert ( 'An amount is required' );
                return false;
            }
            if( !aform['item_name'].value || 0 === aform['item_name'].value.length || /^\s*$/.test( aform['item_name'].value ) ) {
                alert ( 'Event type is required' );
                return false;
            }
            if( !aform['name_first'].value || 0 === aform['name_first'].value.length || /^\s*$/.test( aform['name_first'].value ) ) {
                alert ( 'Full name is required' );
                return false;
            }
            if( !aform['phone'].value || 0 === aform['phone'].value.length || /^\s*$/.test( aform['phone'].value ) ) {
                alert ( 'Phone number is required' );
                return false;
            }
            if( !aform['email_address'].value || 0 === aform['email_address'].value.length || /^\s*$/.test( aform['email_address'].value ) ) {
                alert ( 'An amount is required' );
                return false;
            }
             aform['amount'].value = Math.round( aform['custom_amount'].value*Math.pow( 10,2 ) )/Math.pow( 10,2 );
        }
        
        return (
         <div className={'registration'}>
          <form action="https://www.payfast.co.za/eng/process" name="form_4b4cc514cb4f65acbe66b23c64b9e9dd" onsubmit={()=>gotoPayfast( this )}  method="post">
      
            <input type="hidden" name="cmd" value="_paynow"/>
            <input type="hidden" name="receiver" value="mabasolindaniricardo@gmail.com"/>
            <input type="hidden" name="item_name" value={event_name}/>
            <input type="hidden" name="name_first" value={firstName}/>
            <input type="hidden" name="province" value={province}/>
            <input type="hidden" name="phone" value={province}/>
            <input type="hidden" name="item_description" value='Burning Bush Ministries registration for preparation of the event'/>
            <input type="hidden" name="return_url" value="https://bbmdiscipletracker.github.io/thankyou"/>
            <input type="hidden" name="cancel_url" value="https://bbmdiscipletracker.github.io/thankyou"/>
            <input type="hidden" id="notify_url" name="notify_url" value="http://https://bbmdiscipletracker.github.io/payment_notify"/>
            <input type="hidden" id="merchant_id" name="merchant_id" value="15223494"/>
            <input type="hidden" id="merchant_key" name="merchant_key" value="24n97jg15vygw"/>
            <input type="hidden" id="email_confirmation" name="email_confirmation" value="1"/>
            <input type="hidden" id="payment_id" name="payment_id" value="WIPEOUT"/>
            <input type="hidden" id="confirmation_address" name="confirmation_address" value="mabasolindaniricardo@gmail.com"></input>
            
            <table className={'table'}>
                        
                    <div><h2>Events Registration</h2></div>
                    
                    <div>Please select event:</div>
                    
                    <tr><td>
                        <div class="select">
                        <select required id="item_name" name="item_name" onChange={()=>this.addTotal()}>
                            <option value="580-Harvest">
                            Harvest
                            </option>
                            <option value="350-Passover">
                            Passover 
                            </option>
                            <option value="300-Regional Convention">
                            Regional Convention
                            </option>
                            <option value="50-Other">
                            Other
                            </option>
                        </select>
                        </div>
                    </td></tr>
                    
                    <div>First Name:</div>
                    <tr><td><input required id="name_first" name="name_first"  onChange={()=>this.addName()} type="text" placeholder={firstName} value={firstName} /></td></tr>

                   {/* <div>Province:</div>
                    <tr><td><div class="select">
                        <select id="province" name="province" onChange={()=>this.setProvince()}>
                            <option value="KwaZulu-Natal">
                            KwaZulu-Natal
                            </option>
                            <option value="Gauteng">
                            Gauteng 
                            </option>
                            <option value="Eastern Cape">
                            Eastern Cape
                            </option>
                            <option value="Western Cape">
                            Western Cape
                            </option>
                            <option value="Limpopo">
                            Limpopo
                            </option>
                            <option value="Northern Cape">
                            Northern Cape  
                            </option>
                            <option value="Mpumalanga">
                            Mpumalanga
                            </option>
                            <option value="North West">
                            North West
                            </option>
                            <option value="Free State">
                            Free State
                            </option>
                        </select>
                        </div></td></tr>
                     */}
                    <div>Email:</div>
                    <tr><td><input required id="email_address" name="email_address" type="email" onChange={()=>this.addEmail()} placeholder={email} value={email}/></td></tr>

                   <div>Mobile:</div>
                    <tr><td><input required minlength='10' id="phone" name="phone"  pattern="[0-9]+" type="text" onChange={()=>this.addPhone()} placeholder={phone} value={phone}/></td></tr>
                     {/* 
                    <tr><td><label>City:</label></td></tr>
                    <tr><td><input id="custom_str4" name="custom_str4" required="" type="text" value=""/></td></tr> */}

                    <div>Amount:</div>
                    <tr><td><input required id="amount" name="amount" type="text"  pattern="[0-9.]+" onChange={()=>this.changeCustomAmount()} value={amount}/></td></tr>
                 
                    <tr><td  class="inpurow"><input type="submit" class="btn ghost-black" alt="Pay Now" title="Pay Now with PayFast" value="Pay Now"/> </td></tr>
                 </table>

          </form>
          </div>
        );
    }
}
