import React, { Component } from "react";
import { Switch, Route, Redirect } from "react-router-dom";
import "./Home.scss";
import { withRouter } from "react-router-dom";
import * as pageUrls from "../constants/pageUrls";
import $ from "jquery";
import LandingPage from "../LandingPage/LandingPage";
import BottomNav from "../Header/BottomNarBar";
export default class Home extends Component {
 constructor(props){
    super(props);

    this.state ={
        shouldDisplayBackground: true
    }
 }

  displayMenu = menuList => {
    return menuList.map((pageList, index) => {
      let { history } = this.props;
      return (
        <li key={index}>
         <a href={pageList.path} style={{'--index':index }}> {pageList.page}</a>
        </li> 
      );
    });
  };

 
 
  

  render() {
    let {shouldDisplayBackground=true} =this.state;
    let MenuList = [
      { page: "Home", path: "/home" },
      { page: "About", path: "/about" },
      { page: "Events", path: "/events" },
      { page: "Sermons", path: "/sermons" },
      { page: "Ministries", path: "/ministries" },
      { page: "Registration", path: "registration" },
      { page: "Login", path: "/login" }
    ];  
    var displayBg = () => {
      let {shouldDisplayBackground=true} = this.state;
      return shouldDisplayBackground && <LandingPage />
    }
    
 
    return (
      <div className={'main'}>
        <div className={'content-menu'}>
            {displayBg()}
        </div>
      
        <div className={'menu-wrap'}>
          <input type="checkbox" className={'toggle'} onClick={()=>this.setState({shouldDisplayBackground: !shouldDisplayBackground})}/>
          <div className={'hamburger-wrap'} >
            <div className={'hamburger'} />
          </div>
          <div className={'menu'}>
            <ul>
              {this.displayMenu(MenuList)}
            </ul>
          </div>
        </div>
      </div>
    );
  }
}
