import * as actionTypes from '../actions/actionTypes';
import { getUrl, login, logout  } from '../middleware/connectAPI';

let API_URL = getUrl();
var _store = null;
const dispatchAction = (actionType, res) => _store.dispatch({ type: actionType, value: res });



const signalRMiddleware = (store) => next => async (action) => {
    _store = store;
    await handleActionType(action.type, action.value);
    return next(action);
};

const handleActionType = async (type, value) => {
    switch (type) {

    case actionTypes.SEND_LOGIN:
        let authenticated = await login(value.Username, value.PIN);
        if (authenticated) {
            try {
                //update state - LOGGEDIN
                console.log('LoggedIn succesful');
            } catch (err) {
                console.log('******************:', err);
            }
        }
        break;
    case actionTypes.SEND_LOGOUT:
        await logout();
        break;
    default:
        break;
    }
}


export default signalRMiddleware;